.menu {
    &-nav {
        margin-right: -30px;
        display: flex;
        overflow: auto;
        margin-top: 30px;
        margin-bottom: 15px;
        -ms-overflow-style: none;
        scrollbar-width: none;
      &::-webkit-scrollbar {
        width: 0;
        background: transparent;
      }
        &__btn {
            font-weight: 700;
            font-size: 32px;
            line-height: 37px;
            color: $date;
            margin: 0;
            padding: 0;
            border: 0;
            outline: none;
            background-color: transparent;
            white-space: nowrap;
            margin-right: 30px;
            cursor: pointer;
            &:last-child {
                margin-right: 0;
            }
        }
    }
    &-information {
        border-top: 4px solid rgba(255, 255, 255, 0.3);
        padding: 30px 0;
        &__title {
            font-weight: 700;
            font-size: 21px;
            line-height: 24px;
            color: $white;
            padding-bottom: 30px;
        }
        &__text {
            font-weight: 400;
            font-size: 19px;
            line-height: 30px;
            color: $white;
        }
}}
@media screen and (min-width:768px) {
    .menu {
        &-nav {
            margin-right: -30px;
            padding-top: 30px;
            padding-bottom: 30px;
            &__btn {
                font-size: 41px;
                line-height: 46px;
            }
        }
        &-information {
            padding: 60px 0;
            &__title {
                font-size: 31px;
                line-height: 37px;
            }
        }
    }
}
@media screen and (min-width:850px) {
    .menu {
        display: flex;
        padding-top: 58px;
        margin-bottom: 60px;
        &-nav {
            width: 48%;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            padding-right: 60px;
            margin: 0;
            padding-top: 16px;
            padding-bottom: 0;
            &__btn {
                margin: 0;
                margin-bottom: 30px;
                &:last-child {
                margin-bottom: 15px;
                }
            }
        }
        &-information {
            width: 52%;
            padding-left: 60px;
            border-left: 4px solid rgba(255, 255, 255, 0.3);
            border-top: none;
            padding-bottom: 0;
            padding-top: 10px;
        }
    }
}
@media screen and (min-width:992px) {
    .menu {
        margin-bottom: 58px;
        &-nav {
            width: 42%;
            padding-top: 20px;
            &__btn {
                font-size: 42px;
                line-height: 46px;
            }
        }
        &-information {
            width: 58%;
            padding-top: 46px;
            &__text {
                width: 83%;
            }
        }
    }
}

