.is-active__lang {
  background: $hover;
  border-radius: 8px;
}
.is-active__text {
  background-image: linear-gradient(90.13deg, #EF8080 0.84%, #F0145A 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.is-active__slider {
  background-color: $blue;
}
.is-active__menu {
  color: $hover;
}
